
.MuiPaper-root.MuiAccordion-root.MuiAccordion-rounded.MuiPaper-elevation1.MuiPaper-rounded{
    box-shadow: 0px 0px 3px rgba(0, 12, 78, 0.288) !important;
    border-radius: 10px !important;
    border: 2px solid transparent;
    border-radius: 10px !important;
    filter: saturate(0) !important;
    transition: all .3s;
}
.MuiPaper-root.MuiAccordion-root.MuiAccordion-rounded.MuiPaper-elevation1.MuiPaper-rounded:hover{
    cursor: pointer;

    filter: saturate(1) !important;
    border: $borderGeneral;
    box-shadow: 1px 1px 10px rgba(0, 12, 78, 0.466);
}

.MuiPaper-root.MuiAccordion-root.Mui-expanded.MuiAccordion-rounded.MuiPaper-elevation1.MuiPaper-rounded{
    filter: saturate(1) !important;
    border: $borderGeneral;
    box-shadow: 1px 1px 10px rgba(0, 12, 78, 0.466) !important;
    color: var(--textGeneral);
}
.MuiPaper-root.MuiAccordion-root.Mui-expanded.MuiAccordion-rounded.MuiPaper-elevation1.MuiPaper-rounded .MuiTypography-root.acordion__title.MuiTypography-body1{

    color: var(--secondary);
}

.MuiPaper-root.MuiAccordion-root.MuiAccordion-rounded.MuiPaper-elevation1.MuiPaper-rounded::before{
   height: 0;
}

.MuiButtonBase-root.MuiAccordionSummary-root{

}
.MuiSvgIcon-root{
    fill: var(--secondary) !important;
    color: var(--secondary);
}

.acordion__title{
    @include fontNormal;
    margin-left: 10px !important;

}

.MuiPaper-root.MuiAccordion-root.MuiAccordion-rounded.MuiPaper-elevation1.MuiPaper-rounded:hover .acordion__title{
    color: var(--secondary);
    
}

.MuiAccordionSummary-content{
    margin: initial !important;
}
 
.pokefilter{
   width: 100%;
}
.pokefilter button{
    border: none;
    background-color: transparent;
    outline: none;
    width: 100%;
}
.pokefilter button:hover{
   cursor: pointer;
}

.pokefilter__checks-continer{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 14px;
    margin-bottom: 12px;
    border-bottom: 1px solid var(--primary);
    padding-bottom: 21px;
}

.pokefilter__title-types {
    width: 100%;
    display: block;
    text-align: initial;
    margin: 10px 0 20px;
}
.MuiAccordionSummary-root.Mui-expanded {
    min-height: 48px !important;
    border-bottom: 1px solid var(--primary);
}

.filter__checks-group{
     
}

[class*="MuiBox-root MuiBox-root-"]{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin: 12px -10px 0;
}

.MuiFormControlLabel-root{
    margin: initial !important;
}

.filter__slider {
    width: 100%;
    text-align: initial;
}

.filter__slider--row{
    display: flex;
    align-items: center;
    font-size: 1rem;;
}

.MuiTypography-gutterBottom{
    margin-bottom: initial !important;
}

.MuiSlider-rail{
    height: 4px !important;
    border-radius: 20px !important;
    background-color: rgb(179, 179, 179) !important;
}

.MuiSlider-track{
    height: 4px !important;
    background-color: var(--secondary)!important;
}
.MuiSlider-thumb{
    background-color: var(--secondary)!important;
    margin-top: -4px !important;
}